.footer {
    border-top: 1px solid #c7e8e9;
    padding: 5rem 0;
}

.flex {
    display: flex;
    justify-content: center;
    align-items: center;
}
.flex img {
    width: 65px;
    border-radius: 50%;
}

.footer ul {
    margin: 0 auto;
    text-align: start;
}

.footer ul li {
    margin-bottom: 10px;
}

.footer ul li a {
    transition: color 0.5s;
}

.footer ul li a:hover {
    color: #d8bb3d;
}

.icons a {
     margin-left: 15px;
}

.icons a:hover {
    color:  #d8bb3d;
}

@media screen and (max-width: 768px) {
    .flex {
        flex-direction: column-reverse;
        text-align: center;
    }

    .footer ul {
        display: flex;
    }

    .footer ul li {
        margin: 20px 5px 10px;
    }
}
    
